import React from "react";
import { Container, Row } from "react-bootstrap";
import Arrow from "../shared/arrow/arrow";
import "./portfolio.scss";
import FlauntThumbnail from "../../assets/portfolio/falunt/thumb.jpg";
import FlauntFull from "../../assets/portfolio/falunt/full.jpg";
import OnlineSportsThumbnail from "../../assets/portfolio/online-sports/thumb.jpg";
import OnlineSportsFull from "../../assets/portfolio/online-sports/full.jpg";
import DavidThumbnail from "../../assets/portfolio/david/thumb.jpg";
import DavidFull from "../../assets/portfolio/david/full.jpg";

import ModalComponent from "../shared/modal/ModalComponent";

const projects = [
  {
    image: FlauntFull,
    title: "Flaunt it Salon",
    thumbnail: FlauntThumbnail
  },
  {
    image: OnlineSportsFull,
    title: "Online Sports",
    thumbnail: OnlineSportsThumbnail
  },
  {
    image: DavidFull,
    title: "David's",
    thumbnail: DavidThumbnail
  }
];

export default function Portfolio() {
  let portfolio = [];
  projects.map((project, index) => {
    portfolio.push(
      <ModalComponent
        thumbnail={project.thumbnail}
        image={project.image}
        title={project.title}
        key={index}
        alt={project.title}
      />
    );
    return true;
  });
  return (
    <div className="portfolio-wrapper wrap" id="portfolio">
      <Arrow color="feac58" />
      <Container>
        <h1 className="title">Portfolio</h1>
        <Row className="mb-3">{portfolio}</Row>
        <span className="pre-title portfolio-cta">
          <a href="#contact">Get in touch</a> if you want to see more!
        </span>
      </Container>
    </div>
  );
}
