import React from 'react';
import './App.scss';

import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import Home from './components/home/Home';
import NotFound from './components/not-found/NotFound'



function App() {
  return (

    <Router>
      <div className="App">
        <Switch>
          <Route path="/" exact>
            <Home />
          </Route>
          <Route component={NotFound}>
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
