import React from "react";
import "./navbar.scss";
import navigate from "../../assets/navigate.png";

var x = window.matchMedia("(min-width: 900px)");

export default function Navigation() {
  const desktop = (
    <div className="mobileMenuWrap">
      <ul className="list-unstyled">
        <li>
          <img src={navigate} alt="Navigate" className="navigate img-fluid" />
        </li>
        <li>
          <a href="#home">Home</a>
        </li>
        <li>
          <a href="#about-me">About me</a>
        </li>
        <li>
          <a href="#what-i-do">Services</a>
        </li>
        <li>
          <a href="#portfolio">Portfolio</a>
        </li>
        <li>
          <a href="#contact">Contact</a>
        </li>
      </ul>
    </div>
  );

  // const mobile = (
  //   <Navbar bg="dark" variant="dark">
  //     <Navbar.Brand href="#home">PurpleHaze Graphics</Navbar.Brand>
  //     <Navbar.Toggle />
  //     <Navbar.Collapse className="justify-content-end"></Navbar.Collapse>
  //   </Navbar>
  // );
  return <div>{x.matches ? desktop : ""}</div>;
}
