import React, { useState } from "react";
import { Modal, Button, Card, Col } from "react-bootstrap";
import "./ModalComponent.scss";

export default function ModalComponent(props) {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Col sm="4" className="mb-4 portfolio-card" onClick={handleShow}>
        <Card className="portfolio-card" border="none">
          <Card.Img variant="top" src={props.thumbnail} className="img-fluid" />
        </Card>
      </Col>

      <Modal show={show} onHide={handleClose} size="xl">
        <Modal.Header closeButton>
          <Modal.Title>{props.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <img src={props.image} alt="" className="img-fluid full-project" />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="outline-secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
