import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import "./Copyright.scss";

export default function Copyright() {
  let date = new Date();
  return (
    <div className="copyright-wrapper">
      <Container>
        <Row>
          <Col>
            <p className="mt-3">
              &copy; {date.getFullYear()} PurpleHaze Graphics
            </p>
          </Col>
        </Row>
      </Container>
    </div>
  );
}
