import React from "react";
import "./about.scss";
import { Container, Row, Col } from "react-bootstrap";

import Arrow from "../shared/arrow/arrow";
import AboutImage from "../../assets/about-purplehaze.png";

export default function About() {
  return (
    <div className="about-wrapper wrap" id="about-me">
      <Arrow color="ff7675" />
      <Container>
        <h1 className="pre-title">
          Freelance website designer and developer based out of Bangalore
        </h1>
        <Row>
          <Col xs="12" sm="6">
            <div className="row"></div>
            <h1 className="title">Who am I?</h1>
            <h2 className="sub-title">Get to know me better</h2>
            <h3>
              I am a Fullstack Developer, User Interface and User Experience
              Specialist based out of Bangalore with over 15 years of experience
              working for MNCs like ITC, Aviva, WNS, Essar, Microsoft and
              several hottest start-ups.
            </h3>
            <p>
              Stepping out of the glass door and my cozy cubicle, PurpleHaze
              Graphics is my honest effort to dig deep into my passion for
              design and code.
            </p>
            <p>
              I deliver what I promise at a price that’s fair and reasonable.
            </p>
            <p className="post-content">And yes, you can expect awesomeness!</p>
          </Col>
          <Col>
            <img
              src={AboutImage}
              alt="About PurpleHaze Graphics"
              className="img-fluid"
            />
          </Col>
        </Row>
      </Container>
    </div>
  );
}
