import React, { Component } from "react";
import "./Contact.scss";

import axios from "axios";

import Arrow from "../shared/arrow/arrow";
import { Container, Row, Col } from "react-bootstrap";
import QiWorks from "../../assets/partners/qiworks.png";
import BeingSoraya from "../../assets/partners/being-soraya.jpg";
import BudDigital from "../../assets/partners/bud-digital.png";
import Contented from "../../assets/partners/contented.png";

import ContactForm from "./form/ContactForm";

export default class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      subject: "Message from PurpleHaze Graphics",
      to: "salilchhetri@gmail.com",
      emailSent: false,
      formData: {}
    };
  }

  sendMessage = data => {
    axios
      .post("https://darjeelicious.com/api/email", data)
      .then(response => {
        console.log("RESPONSE", response);
        this.setState({ emailSent: true, formData: data });
      })
      .catch(error => {
        console.log("Failure", error);
        alert("Oops! Something went wrong. Please call for enquiry.");
      });
  };

  render() {
    let form;
    if (this.state.emailSent) {
      form = (
        <div className="success-message">
          <h4 className="post-content">
            Hey <span>{this.state.formData.name}</span>, <br /> thanks for the
            message.
          </h4>
          <p className="sub-title">I will get back to you shortly...</p>
        </div>
      );
    } else {
      form = (
        <>
          <h4 className="pre-title">Shoot a message</h4>
          <ContactForm handleSubmit={data => this.sendMessage(data)} />
        </>
      );
    }
    return (
      <div className="contact-wrapper" id="contact">
        <Arrow color="273c67" />
        <Container>
          <Row>
            <Col>
              <h1 className="title">Contact Me</h1>
              <div className="post-content">
                Liked my webiste and portfolio?
              </div>
              <div className="post-content">
                Would you like to have something like this for yourself?
              </div>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col sm={6}>
              <div className="with-bottom-border">
                <h4 className="pre-title">Let's talk!</h4>
                <p>
                  Email:{" "}
                  <a href="mailto:salilchhetri@gmail.com">
                    salilchhetri@gmail.com
                  </a>
                  <br />
                  Phone: +91 82964 48337
                </p>
              </div>
              <h4 className="pre-title">The awesome folks I work with:</h4>
              <div className="partner-logos">
                <a
                  href="https://www.budindia.com"
                  target="_blank"
                  className="img-fluid"
                  rel="noopener noreferrer"
                >
                  <img src={BudDigital} alt="Bud Digital" />
                </a>
                <a
                  href="https://www.facebook.com/BeingSoraya"
                  target="_blank"
                  className="img-fluid"
                  rel="noopener noreferrer"
                >
                  <img src={BeingSoraya} alt="Being Soraya" />
                </a>
                <a
                  href="https://www.qiworks.in/"
                  target="_blank"
                  className="img-fluid"
                  rel="noopener noreferrer"
                >
                  <img src={QiWorks} alt="QiWorks Bangalore" />
                </a>
                <a
                  href="http://www.content-ed.in"
                  target="_blank"
                  className="img-fluid"
                  rel="noopener noreferrer"
                >
                  <img src={Contented} alt="Contented" />
                </a>
              </div>
            </Col>
            <Col sm={6}>{form}</Col>
          </Row>
        </Container>
      </div>
    );
  }
}
